import React from 'react';


export default ( {children} )=> {
    return(
        <div className="header">
            <div className="header-camera">
            <img className="header-camera--pic" src="static/icons/myplay-logo-square.png" alt="camera play"/>
            </div>
            <div className="header-dropdown">
            <h1 className="header-dropdown--name">{children}</h1>
                <img className="header-dropdown--pic"src="static/icons/robot.png" alt="robot"/>
            </div>
        </div>
    )   
}


