import React from 'react';


export default ()=> {
    return(
        <div className="container">
            <div className="container-holder">
            <div className="gradient"></div>
            <img className="container-holder--pic" src="static/icons/robot2.png" alt="picture"/>
                  <h1 className="container-holder--text"> </h1>
            </div>
            <div className="form-holder">
               <form action="#" method="post" name="contactForm" className="contactForm" >
                   <div className="group">  
                <input  type="text" name="your-name" className="name" id="name"
                  placeholder="" required="true" />
                 <span className="bar"></span>
                 </div>

                <div className="group">  
                <input  type="text" name="your-surname" className="surname"
                 id="surname" placeholder=" " required="true" />
                 <span className="bar"></span>
                </div>
                 <div className="group">  
                <input  type="password" name="your-password" className="password" 
                id="password" placeholder="" required="true" />
                 <span className="bar"></span>
                 </div>
               <div className="group">  
                <input  type="password" name="new-password" className="new-password"
                 id="new-password" placeholder=" " required="true"  />
                 <span className="bar"></span>
                 </div>
                <div className="group">  
                <input  type="tel" name="your-phone" className="phone" id="phone"
                 placeholder="" required="true" />
                 <span className="bar"></span>
             </div>
               </form>
               <input type="submit" value=" " />
                
            </div>
        </div>
    )   
}


