import React from 'react';

export default ()=> {
    return(
        <div className="footer">
            <footer className="footer">
              <ul className="footer-icons">
                 <li className="footer-icon">
                     <img className="footer-image" src="static/icons/live.svg"alt="live video pic"/>
                     <p> LIVE</p>
                 </li>
                 <li className="footer-icon">
                     <img className="footer-image" src="static/icons/camera.svg"alt="camera"/>
                     <p> </p>
                 </li>
                 <li className="footer-icon">
                     <img className="footer-image" src="static/icons/manage_team.svg"alt="manage_team icon"/>
                     <p> </p>
                 </li>
                 <li className="footer-icon">
                     <img className="footer-image" src="static/icons/games-icon.svg"alt="games-icon"/>
                       <p></p>
                 </li>
              </ul>
            </footer>
        </div>
    )   
}


